

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

nav{ 
  height: 70px;
  display: flex;
  padding: 20px 36px;
  background: #FFFFFF;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

.bold{
  font-weight: bold;
}

.gray{
  color: #918E9B;
}

.logo {
  max-width: 100px;
}
section {
  padding: 30px;
  
}
.hero {
  display: flex;
  flex-direction: column;
}
.hero--photo{
  max-width: 400px;
  align-self: center;
}
.hero--header {
  margin-bottom: 16px;
}
.hero--text{
  margin-top: 0px;
}

.cards-list{
  display: flex;
  flex-wrap: nowrap;
  gap: 20px;
  overflow-x: auto;
  
}

.card {
  width: 175px;
  font-size: 12px;
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
  position: relative;
}
.card--image{
  width: 100%;
  border-radius: 9px;
  margin-bottom: 9px;
  height: 235px
}
.card--title{
  overflow: hidden;
  text-overflow: ellipsis;
}
.card--stats {
  display: flex;
  align-items: center;
}

.card--star{
  width: 14px;
}
.card--price{
  margin-top: auto;
}

.card--badge{
  position: absolute;
  top: 6px;
  left: 6px;
  background-color: white;
  padding: 5px 7px;
  border-radius: 2px;
  font-weight: bold;
}


